.timeline-body > ul {
  padding:20px;
  margin-bottom: 0;
}
.timeline-body > p + p {
  margin-top: 5px;
}
.timeline-footer{
  padding:10px;
  background-color:#f4f4f4;
}
.timeline-footer > a{
  cursor: pointer;
  padding: 12px;
  text-decoration: none;
}
